import React from 'react';

import Container from '../../components/Container';
import Box from '../../components/Box';
import Flex from '../../components/Flex';
import SectionTitle from '../../components/SectionTitle';
import BackgroundImage from '../../components/BackgroundImage';
import Border from '../../components/Border';
import Text from '../../components/Text';

import plane from './plane.png';
import proxy from './proxy.png';
import warehouse from './warehouse.png';

const contents = [
  {
    bg: plane,
    title: '',
    text1: `CCIC
    `,
    text2: [
      '',
      ' : ',
      '',
      '',
    ],
  },
  {
    bg: proxy,
    title: '',
    text1: `
    `,
    text2: [
      ' : ',
      '',
      '',
    ],
  },
  {
    bg: warehouse,
    title: '',
    text1: `20
     `,
    text2: [
      '',
      '20,000 /3,000',
    ],
  }
];

const Service = () => (
  <Container my="4em">
    <SectionTitle subtitle="SERVICE">
      
    </SectionTitle>
    <Flex
      my="2em"
      flexDirection={['column', 'column', 'row']}
    >
      {contents.map((content, index) => (
        <Box
          key={index}
          px="1em"
          w={[1, 1, 1 / 3]}
          my="1em"
        >
          <Box border="1px solid black" w={1} pb="80%" position="relative">
            <Box
              position="absolute"
              content=" "
              top="0"
              bottom="0"
              left="0"
              right="0"
            >
              <BackgroundImage
                src={content.bg}
                width={1}
                ratio={15 / 33}
              >
                <Flex
                  position="absolute"
                  content=" "
                  top="0"
                  bottom="0"
                  left="0"
                  right="0"
                  justifyContent='center'
                  align="center"
                >
                  <Box px="1em">
                    <Text color="white" fontSize="1.5em">{content.title}</Text>
                    <Border
                      borderBottom='2px solid'
                      borderColor='red'
                      w="100px"
                      mx="auto"
                      height="10px"
                    />
                    <Text color="white" fontSize="0.75em">
                      {content.text1}
                    </Text>
                  </Box>
                </Flex>
              </BackgroundImage>
              <Box pb="35%"  position="relative">
                <Flex
                  position="absolute"
                  top="0"
                  bottom="0"
                  left="0"
                  right="0"
                  justifyContent="center"
                  align="center"
                >
                  <Text px="1em" fontSize=".75em">
                    {content.text2.reduce((all, t, index) => {
                      if (index === 0) return [t];
                      return [...all, <br key={index} />, t];
                    })}
                  </Text>
                </Flex>
              </Box>
            </Box>
          </Box>
        </Box>
      ))}
    </Flex>
    <Border
      borderBottom="1px solid"
      borderColor="red"
      height="2px"
      mx="auto"
    />
    <Border
      borderBottom="1px solid"
      borderColor="red"
      height="10px"
      mx="auto"
    />
  </Container>
);

export default Service;



